<template>
  <dynamic-form
    :key="rerender"
    v-if="parentModulePath"
    :parent-module-path="parentModulePath"
    :item="item"
    :title="title"
    :isEdit="isEdit"
    :onSave="onSave"
    :onCancel="onCancel"
    moduleName="device"
    :initialFormValues="formValues"
  ></dynamic-form>
</template>
<script>
import DynamicForm from '../../FormComponents/dynamicForm.vue';
export default {
  name: 'rf-device-form',
  props: ['isEdit', 'onCancel', 'onSave', 'item', 'isSaving', 'immediateParent'],
  data: () => ({
    formValues: {},
    rerender: 0,
    parentModulePath: null,
  }),
  components: { DynamicForm },
  computed: {
    lowerCaseTitle() {
      return 'device';
    },
    titleCase() {
      return 'Device';
    },
    title() {
      return this.isEdit ? `Edit ${this.titleCase}` : `Create ${this.titleCase}`;
    },
    saveTitle() {
      return this.isEdit ? 'Save' : 'Create';
    },
  },
  mounted() {
    if (this.isEdit && this.item) {
      this.formValues = {
        ...this.item,
      };
      this.rerender++;
    }
    if (this.immediateParent) {
      this.parentModulePath = this.immediateParent.plugin_name;
      this.formValues = {
        ...this.formValues,
        plugin_name: this.immediateParent.plugin_name,
        network_uuid: this.immediateParent.uuid,
      };
      this.rerender++;
    }
  },
};
</script>
